.container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
  
}
.draggable {
  position: absolute;
  z-index: 1;
  left: 20px;
  top: 20px;
  width: 100px;
  height: 100px;
  background-color: green;
}
.timer {
    &-toggle__input {
        position: relative;
        top: .5rem;

        -webkit-appearance: none;
        appearance: none;
        width: 4.4rem;
        height: 2.4rem;
        border-radius: 1.5rem;
        background: transparent;
        cursor: pointer;

        border: 0.2rem solid var(--color-white);

        @include transition(0.4s);

        &::before {
            width: 1.6rem;
            height: 1.6rem;
            border-radius: 50%;
            content: "";
            position: absolute;
            top: .2rem;
            left: 7%;

            @include transition(0.4s);

            background-color: var(--color-white);
        }

        &:checked::before {
            left: 55%;
        }

        &:focus {
            outline: none;
        }
    }
}